// Create and setup your form here
 
<template>
  <div>

    
    <flow-form
      ref="flowform"
      v-bind:language="language"
      v-on:complete="onComplete"
      v-on:submit="onQuizSubmit"
      
      v-bind:standalone="true"
    >
      <question 
        v-for="(question, index) in questions" 
        v-bind="question" 
        v-bind:key="'m' + index" 
        v-model="question.model"
      >
      </question>
      
      <!-- Custom content for the Complete/Submit screen slots in the FlowForm component -->
      <template v-slot:complete>
        <p>
          <span v-if="!submitted" class="fh2">  !</span>
          <span v-if="server_result == 'passed'" class="fh2"> !</span>
          <span v-if="server_result == 'failed'" class="fh2"> !</span>
          <span v-if="server_result == 'error'" class="fh2"></span>
          <span v-if="!submitted" class="f-section-text">
                 .      "".
          </span>
        </p>
      </template>

      <!-- We've overriden the default "completeButton" slot content -->
      <template v-slot:completeButton>
        <div class="f-submit" v-if="!submitted">
          <button
            class="o-btn-action"
            ref="button"
            type="submit"
            href="#"
            v-on:click.prevent="onQuizSubmit()"
            aria-label="Press to submit">
            <span></span>
          </button>
          <a 
            class="f-enter-desc"
            href="#"
            v-on:click.prevent="onQuizSubmit()"
            v-html="language.formatString(language.pressEnter)">
          </a>
        </div>
        <p class="text-success" v-if="submitted && server_result == 'nothing'">    ...</p>
        <p class="text-success" v-if="server_result == 'passed'">     ! </p>
        <p class="text-success" v-if="server_result == 'failed'">!       . ,         ,   .</p>
        <p class="text-success" v-if="server_result == 'error'">    .<br>   -    (,      ). ,    .</p>
        <button
            class="o-btn-action"
            ref="button"
            type="submit"
            v-on:click.prevent="goForward()"
            v-if="server_result == 'passed' || server_result == 'failed'"
            aria-label="Press to submit">
            <span></span>
          </button>
      </template>
    </flow-form>
  </div>
</template>

<script>
  /*
    Copyright (c) 2020 - present, DITDOT Ltd. - MIT Licence
    https://www.ditdot.hr/en
  */
  import { FlowForm, Question, LanguageModel } from '@ditdot-dev/vue-flow-form'

  global.jQuery = require('jquery');
  var $ = global.jQuery;
  window.$ = $;
  var quiz_bundle;

  var getUrlParameter = function getUrlParameter(sParam) {
    var sPageURL = window.location.search.substring(1),
        sURLVariables = sPageURL.split('&'),
        sParameterName,
        i;

    for (i = 0; i < sURLVariables.length; i++) {
        sParameterName = sURLVariables[i].split('=');

        if (sParameterName[0] === sParam) {
            return sParameterName[1] === undefined ? true : decodeURIComponent(sParameterName[1]);
        }
    }
    return false;
  };

  var endpoint = getUrlParameter('endpoint'),
      token = getUrlParameter('token'),
      user_id = getUrlParameter('user_id'),
      quiz_id = getUrlParameter('quiz_id') || "Situations";

  quiz_id = quiz_id.replace(/[^A-Za-z]/ig, '');
  quiz_id = quiz_id.toLowerCase();

  $.ajax({
    dataType: "json",
    async: false,
    url: './pack/quizes/' + quiz_id + '.json',
    success: function(data){
      quiz_bundle = data;
      console.log(quiz_bundle)
      console.log(quiz_bundle.answers)
    }
  });

  export default {
    name: 'TestTest',
    components: {
      FlowForm,
      Question
    },
    data() {
      return Object.assign(
        {language: new LanguageModel({
          multipleChoiceHelpTextSingle: '',
          pressEnter: ' :enterKey',
          continue: '',
          successText: "      . ,    ."
        })}, {score: 0, server_result: "nothing" }, quiz_bundle);
    },
    methods: {
      /* eslint-disable-next-line no-unused-vars */
      onComplete(completed, questionList) {
        // This method is called whenever the "completed" status is changed.
        this.completed = completed
      },
      arrayEquals(a, b) {
        return Array.isArray(a) && Array.isArray(b) && a.length === b.length &&
          a.every((val, index) => val === b[index])
      },
      goForward(){
        console.log("Processing with goForward()...")
        parent.postMessage({identifier: "a01_iframesource_quiz", load: "go_forward"}, '*');
      },
      calculateScore() {
        console.log("Endpoint URL preview: ", endpoint)
        var questions_filtered = {};
        this.questions.forEach(question => {
          if (question.type != "SectionBreak") {
            questions_filtered[question.id] = question.model
          }
        })
        console.log("Payload.questions_filtered", questions_filtered)
        var ajax_result_state = 0;
        $.ajax({
            url: endpoint,
            async: false,
            method: 'post',
            cache: false,
            data: {user_id: user_id, token: token, step: "quiz", quiz_id: quiz_id, questions_filtered: JSON.stringify(questions_filtered)},
            success: function(data){
                console.log(data)
                if(data.status == "okay"){
                  ajax_result_state = data.state;
                }else{
                  ajax_result_state = "error"
                  alert("Sorry, this request cannot be perfomed right now: Server Error.")
                }
            }
        });
        this.server_result = ajax_result_state
      }, 
      onQuizSubmit() {
        // Set `submitted` to true so the form knows not to allow back/forward
        // navigation anymore.
        this.$refs.flowform.submitted = true
        
        this.submitted = true
        this.calculateScore()
      },
    },
  }
</script>

<style>
  /* Import Vue Flow Form base CSS */
  @import '~@ditdot-dev/vue-flow-form/dist/vue-flow-form.css';
  /* Import one of the Vue Flow Form CSS themes (optional) */
